.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: var(--Mask-Black---30, rgba(0, 0, 0, 0.3));
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  visibility: hidden;
  padding: 16px;
  opacity: 0;
  transform: translateY(-100vh);
  transition: opacity 0.3s ease-in-out;
  @include response(1200px) {
    @media (orientation: landscape) {
      align-items: flex-start;
    }
  }
  &.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
  &__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__box {
    position: relative;
    border-radius: 8px;
    background: var(--Primary-White, #fff);
    width: 100%;
    padding: 40px 16px 16px;
    transform: translateY(20px);
    opacity: 0;
    max-height: 100dvh;
    overflow-y: auto;
    transition: 0.3s ease-in-out;
    .popup.active & {
      transform: translateY(0);
      opacity: 1;
    }
  }
  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
    z-index: 1;
    display: flex;
    @include desktop-hover() {
      svg {
        stroke: #ff3d00;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__tags-nav-holder {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 32px;
  }
  &__tags-nav {
    position: relative;
    list-style: none;
    padding: 4px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background: #f5f5f5;
    gap: 8px;
    li {
      position: relative;
      flex: 0 0 auto;
    }
    a {
      display: block;
      text-decoration: none;
      padding: 4px 12px;
      color: #969696;
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      color: #969696;
      border-radius: 8px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        background: #fff;
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        color: #000;
      }
      &.active {
        background: #fff;
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        color: #000;
      }
    }
  }
  &__tags-options {
    position: relative;
    padding: 0 24px 16px;
  }
  &__tags-option {
    position: relative;
  }
  &__tags-title {
    position: relative;
    margin-bottom: 16px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
  }
  &__tags-description {
    position: relative;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 32px;
    p {
      margin: 0;
    }
  }
  &__tags-list {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  &__tags-item {
    position: relative;
    margin-bottom: 32px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__tags-item-title {
    position: relative;
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 12px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__tags-item-description {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    p {
      margin: 0;
    }
  }
}

.return-policy {
  position: relative;
  padding: 16px 24px;
  &__head {
    position: relative;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    gap: 16px;
    @include response(767px) {
      display: block;
    }
  }
  &__img {
    position: relative;
    flex: 0 0 32px;
    display: flex;
  }
  &__head-title {
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    span {
      color: #ff3d00;
    }
  }
  &__body {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 32px;
    p {
      margin: 0;
    }
  }
  &__btn {
    position: relative;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    cursor: pointer;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: var(--Primary-White, #fff);
    padding: 7px 12px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #000;
    }
  }
}

.notify-me {
  position: relative;
  padding: 16px 24px;
  &__icon {
    display: block;
    margin: 0 auto 16px;
  }
  &__description {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 20px;
  }
  &__title {
    position: relative;
    margin-bottom: 24px;
    color: #000;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__body {
    margin-bottom: 24px;
  }
  &__btn-box {
    text-align: center;
  }
  &__btn {
    position: relative;
  }
  &__footer {
    color: #4d6562;
    text-align: center;
    font-size: 12px;
    line-height: 20px;
    margin-top: 16px;
    a {
      text-decoration: none;
      color: #ff3d00;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
}
