.breadcrumbs {
  list-style: none;
  padding: 8px 0;
  margin: 0 0 30px;
  display: flex;
  overflow-x: auto;
  gap: 32px;
  @include response(1200px) {
    gap: 20px;
    margin-left: -16px;
    margin-right: -16px;
    padding-left: 16px;
    padding-right: 16px;
    margin-bottom: 24px;
  }
  li {
    flex: 0 0 auto;
    a {
      display: block;
      text-decoration: none;
      position: relative;
      color: #454545;
      line-height: 16px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: #ff3d00;
      }
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        margin-top: -4px;
        right: -19px;
        width: 0px;
        height: 0px;
        border-style: solid;
        border-width: 4px 0 4px 6px;
        border-color: transparent transparent transparent #b3bdbc;
        transform: rotate(0deg);
        @include response(1200px) {
          right: -14px;
        }
      }
    }
    span {
      display: block;
      color: #454545;
      line-height: 16px;
    }
  }
}
