.search {
  flex: 0 0 520px;
  width: 520px;
  @include response(1200px) {
    width: auto;
    flex: none;
  }
  &__mobile-btn {
    display: none;
    @include response(1200px) {
      display: flex;
    }
  }
  &__mobile-btn-open {
    @include response(1200px) {
      fill: #fff;
      .search.active-mobile & {
        display: none;
      }
    }
  }
  &__mobile-btn-close {
    @include response(1200px) {
      display: none;
      .search.active-mobile & {
        display: block;
      }
    }
  }
  &__form-holder {
    position: relative;
    @include response(1200px) {
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      height: calc(100dvh - 57px);
      background: rgba(0, 0, 0, 0.3);
      z-index: 3;
      opacity: 0;
      visibility: hidden;
      display: flex;
      flex-direction: column;
      overscroll-behavior: contain;
      .search.active-mobile & {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__form {
    position: relative;
    @include response(1200px) {
      flex: 0 0 auto;
    }
  }
  &__input {
    width: 100%;
    border-radius: 99px;
    border: 1px solid var(--main-30, #b3bdbc);
    background: var(--Primary-White, #fff);
    padding: 4px 200px 4px 24px;
    height: 42px;
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      border-radius: 0;
      height: 60px;
    }
    &::placeholder {
      color: var(--main-50, #80918f);
      font-weight: 400;
    }
    &:focus {
      border: 1px solid var(--Primary-Accent, #ff3d00);
    }
  }
  &__btn {
    position: absolute;
    background: transparent;
    right: 8px;
    top: 50%;
    margin-top: -16px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
    padding: 0;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background: #ff3d00;
      top: 14px;
      margin-top: 0;
    }
    @include desktop-hover() {
      background: #ff3d00;
      svg {
        fill: #fff;
      }
    }
    svg {
      fill: #4d6562;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        fill: #fff;
      }
    }
    &.loading {
      background: #ff3d00;
      svg:not(.search__btn-loading) {
        opacity: 0;
      }
    }
  }
  &__btn-loading {
    display: none;
    .loading.search__btn & {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -9px;
      margin-left: -9px;
      fill: #fff;
      animation: loading 1s linear infinite;
    }
  }
  &__radio-btns {
    position: absolute;
    right: 50px;
    top: 50%;
    margin-top: -16px;
    @include response(1200px) {
      position: relative;
      right: auto;
      top: auto;
      margin-top: 0;
      border-radius: 0;
      background: #fff;
      padding: 16px;
    }
    &.hide {
      display: none;
    }
  }
  &__radio-inner {
    display: flex;
    padding: 2px;
    background: #f5f5f5;
    border-radius: 88px;
  }
  &__radio-item {
    position: relative;
    @include response(1200px) {
      width: 100%;
    }
    input {
      display: none;
      &:checked {
        & + span {
          color: #000;
          background: #fff;
          box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        }
      }
    }
    span {
      display: block;
      position: relative;
      color: #969696;
      text-align: center;
      font-family: "Instrument Sans";
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;
      padding: 4px 12px;
      border-radius: 78px;
      cursor: pointer;
      transition: 0.3s ease-in-out;
    }
  }
  &__dropdown {
    position: absolute;
    left: 0;
    width: 100%;
    top: 100%;
    border-radius: 12px;
    overflow: hidden;
    border: 1px solid #eaeaea;
    background: var(--Primary-White, #fff);
    box-shadow: 0px 6px 24px 0px rgba(0, 0, 0, 0.12);
    z-index: 2;
    display: none;
    @include response(1200px) {
      position: relative;
      top: auto;
      left: auto;
      border-radius: 0;
      box-shadow: none;
      border: 0;
      flex: 1 1 auto;
      overflow-y: auto;
      overscroll-behavior: contain;
    }
    .active.search & {
      display: block;
    }
  }
  &__dropdown-empty {
    padding: 20px;
    color: #454545;
    line-height: 20px;
    &.hide {
      display: none;
    }
  }
  &__dropdown-box {
    position: relative;
    display: flex;
    @include response(1200px) {
      display: block;
    }
  }
  &__dropdown-left {
    position: relative;
    padding: 20px;
    background: #f5f5f5;
    width: 180px;
    flex: 0 0 180px;
    @include response(1200px) {
      background: #fff;
      width: 100%;
      padding: 16px;
    }
  }
  &__dropdown-left-title {
    position: relative;
    padding-bottom: 12px;
    margin-bottom: 12px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    border-bottom: 1px solid #eaeaea;
  }
  &__category-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      position: relative;
      margin-bottom: 12px;
      color: #8c8c8c;
      line-height: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    b {
      a {
        color: #000;
      }
    }
    a {
      color: #8c8c8c;
      text-decoration: none;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: #ff3d00;
      }
    }
  }
  &__dropdown-right {
    position: relative;
    flex: 0 0 340px;
    width: 340px;
    padding: 20px;
    @include response(1200px) {
      width: 100%;
      padding: 16px;
    }
  }
  &__dropdown-cell {
    position: relative;
    margin-bottom: 32px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__dropdown-cell-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    padding-bottom: 12px;
    margin-bottom: 12px;
    border-bottom: 1px solid #eaeaea;
  }
  &__dropdown-cell-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__dropdown-cell-link {
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--Primary-Black, #000);
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__product-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  &__product-item {
    position: relative;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__product-link {
    display: flex;
    align-items: center;
    gap: 12px;
    text-decoration: none;
    color: #000;
    @include desktop-hover() {
      .search__product-text {
        color: #ff3d00;
      }
    }
  }
  &__product-img {
    position: relative;
    width: 60px;
    height: 60px;
    flex: 0 0 60px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__product-box {
    position: relative;
  }
  &__product-title {
    display: block;
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
  }
  &__product-text {
    display: block;
    color: #454545;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
  }
  &__brands-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  &__brands-item {
    position: relative;
    margin-bottom: 8px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__brands-link {
    display: flex;
    align-items: center;
    gap: 12px;
    text-decoration: none;
    @include desktop-hover() {
      .search__brands-title {
        color: #ff3d00;
      }
    }
  }
  &__brands-img {
    width: 60px;
    height: 60px;
    flex: 0 0 60px;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: #fff;
    padding: 8px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__brands-title {
    color: #454545;
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
  }
  &__blog-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  &__blog-item {
    position: relative;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__blog-link {
    display: flex;
    align-items: center;
    gap: 12px;
    text-decoration: none;
    @include desktop-hover() {
      .search__blog-text {
        color: #ff3d00;
      }
    }
  }
  &__blog-img {
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    flex: 0 0 76px;
    width: 76px;
    height: 64px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__blog-box {
    position: relative;
  }
  &__blog-title {
    display: block;
    margin-bottom: 4px;
    color: #ff3d00;
    font-size: 12px;
    font-weight: 500;
    line-height: 14px;
  }
  &__blog-text {
    display: block;
    color: #000;
    font-weight: 500;
    line-height: 20px;
    max-height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    transition: .3s ease-in-out;
  }
}

@keyframes loading {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(-360deg);
  }
}
