.rate {
  position: relative;
  &__default {
    position: relative;
    display: flex;
    gap: 2px;
  }
  &__item {
    position: relative;
    display: flex;
    fill: #b3bdbc;
  }
  &__active {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    gap: 2px;
    .rate__item {
      fill: #ff3d00;
    }
  }
  &__item-holder {
    position: relative;
    width: 16px;
    height: 16px;
  }
  &__item-holder-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .small-star & {
    &__item-holder {
      width: 12px;
      height: 12px;
    }
    svg {
      width: 12px;
      height: 12px;
    }
  }
}
