.accordion {
  position: relative;
  &__item {
    position: relative;
  }
  &__item-head {
    position: relative;
    padding: 16px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    border-top: 1px solid #eaeaea;
    cursor: pointer;
    @include desktop-hover() {
      svg {
        stroke: #ff3d00;
      }
    }
    .accordion__item.active & {
      svg {
        transform: scaleY(-1);
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-body {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 16px;
    overflow: hidden;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    white-space: normal;
    display: none;
    @include response(767px) {
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
    }
    .accordion__item.full & {
      -webkit-line-clamp: none !important;
    }
    .accordion__item.active & {
      display: box;
      display: -webkit-box;
    }
    p {
      margin: 0 0 20px;
    }
    ul {
      padding: 0 0 0 25px;
      margin: 0 0 20px;
      &:last-child {
        margin: 0;
      }
      li {
        margin-bottom: 8px;
        span {
          font-weight: 500;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &__item-btn {
    position: relative;
    gap: 8px;
    align-items: center;
    justify-content: center;
    color: var(--Primary-Main, #01231f);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    text-decoration: underline;
    cursor: pointer;
    display: none;
    padding-bottom: 16px;
    @include desktop-hover() {
      text-decoration: none;
    }
    svg {
      transition: 0.3s ease-in-out;
    }
    span {
      &:nth-child(1) {
        display: block;
      }
      &:nth-child(2) {
        display: none;
      }
    }
    .accordion__item.full & {
      svg {
        transform: scaleY(-1);
      }
      span {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
    .accordion__item.active & {
      display: inline-flex;
    }
  }
}

.handling-shipping-row {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
  color: #000;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.text-grey {
  position: relative;
  font-size: 16px;
  line-height: 20px;
  padding: 8px 12px;
  border-radius: 4px;
  background: #f5f5f5;
  @include response(767px) {
    margin-left: -16px !important;
    margin-right: -16px !important;
    padding: 16px;
    border-radius: 0;
  }
}

.link-return-policy {
  display: inline-flex;
  align-items: center;
  vertical-align: top;
  text-decoration: none;
  color: var(--Primary-Main, #01231f);
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  gap: 4px;
  @include desktop-hover() {
    text-decoration: underline;
  }
  span {
    color: #ff3d00;
  }
  svg {
    stroke: #ff3d00;
  }
}
