.thumbs-box {
  position: relative;
  &__holder {
    padding-top: 40px;
    padding-bottom: 40px;
    position: relative;
    border-top: 1px solid #e6e9e9;
    @include response(1200px) {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }
  &__title {
    position: relative;
    margin: 0 0 24px;
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      font-size: 24px;
    }
  }
  &__list {
    &:not(.swiper) {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      @include response(1200px) {
        gap: 8px;
      }
    }
    .swiper-wrapper {
      @include response-min(1201px) {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
      }
    }
  }
  &__item {
    position: relative;
    border-radius: 2px;
    border: 1px solid #eaeaea;
    background: var(--Primary-White, #fff);
    overflow: hidden;
    flex: 0 0 calc(20% - 16px);
    width: calc(20% - 16px);
    .category__list & {
      flex: 0 0 calc(25% - 15px);
      width: calc(25% - 15px);
      @include response(1200px) {
        flex: 0 0 calc(50% - 4px);
        width: calc(50% - 4px);
      }
    }
    @include response(1200px) {
      width: auto;
      flex: 0 0 auto;
    }
  }
  &__item-img {
    position: relative;
    display: flex;
    text-decoration: none;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-label {
    position: absolute;
    left: 0;
    bottom: 8px;
    border: 1px solid var(--main-6, #f0f2f2);
    background: var(--Primary-White, #fff);
    color: var(--Primary-Black, #000);
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    padding: 2px 8px;
  }
  &__item-body {
    position: relative;
    padding: 8px;
  }
  &__item-title-box {
    display: block;
    text-decoration: none;
    color: var(--Primary-Black, #000);
    padding-bottom: 8px;
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 8px;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__item-category {
    display: block;
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__item-title {
    display: block;
    color: #454545;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    position: relative;
    height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  &__item-rate {
    position: relative;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__item-rate-count {
    position: relative;
    color: #454545;
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
    @include response(767px) {
      font-size: 12px;
    }
  }
  &__item-price-box {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 10px;
    line-height: 20px;
  }
  &__item-price {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__item-price-regular {
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-weight: 700;
  }
  &__item-price-new {
    color: #f70808;
    font-size: 16px;
    font-weight: 700;
  }
  &__item-price-old {
    color: var(--main-50, #80918f);
    font-size: 12px;
    font-weight: 500;
    text-decoration: line-through;
  }
  &__item-price-save {
    position: relative;
    border-radius: 4px;
    background: #ffe4e4;
    padding: 2px 4px;
    color: #f70808;
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    @include response(666px) {
      font-size: 10px;
    }
  }
  &__item-price-stock {
    color: var(--main-50, #8f8f8f);
    font-size: 16px;
    font-weight: 700;
    margin-right: 4px;
  }
  &__item-price-label {
    position: relative;
    flex: 0 0 auto;
    color: var(--main-70, #4d6562);
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    border-radius: 4px;
    background: #f5f5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    padding: 2px 4px;
  }
  &__item-btns {
    position: relative;
    display: flex;
    gap: 8px;
  }
  &__wishlist-btn {
    position: relative;
  }
  &__item-add {
    flex: 1 1 auto;
  }
  &__mobile-pagination {
    display: none;
    @include response(1200px) {
      display: flex;
      justify-content: center;
      padding-top: 24px;
    }
  }
}
