.input-style {
  position: relative;
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
  &__label {
    position: relative;
    color: #454545;
    font-family: "Instrument Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
    margin: 0 0 8px;
  }
  &__input {
    width: 100%;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: #fff;
    padding: 12px 16px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #bbb;
    }
    &:focus {
      border-color: #000;
    }
    &::placeholder {
      color: #969696;
      font-weight: 400;
    }
  }
}
