:root {
  --Primary-Main: #01231f;
  --main-30: #b3bdbc;
  --Primary-White: #fff;
  --background-bg-white-pages: #fff;
  --main-50: #80918f;
  --Primary-Accent: #ff3d00;
  --Primary-Black: #000;
  --Mask-Black---30: rgba(0, 0, 0, 0.3);
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
  font-family: "Instrument Sans", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: #000;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  &.ovh {
    overflow: hidden;
  }
  @include response(1200px) {
    ::-webkit-scrollbar {
      display: none;
    }
  }
}

.wrapper {
  position: relative;
  flex: 1 1 auto;
  padding-bottom: 40px;
  @include response(1200px) {
    max-width: 100%;
    width: 100%;
    overflow: hidden;
    padding-bottom: 20px;
  }
}

.container {
  max-width: 1312px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

.title {
  position: relative;
  font-family: "Instrument Sans";
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 36px;
  margin: 0;
  @include response(1200px) {
    font-size: 24px;
  }
}

