.filters {
  position: relative;
  border-radius: 4px;
  border: 1px solid #eaeaea;
  border-bottom: 0;
  background: #fff;
  font-family: "Instrument Sans";
  @include response(1200px) {
    height: 100%;
    border: 0;
    border-radius: 0;
    display: flex;
    flex-direction: column;
  }
}

.filters-top-wrapper {
  position: relative;
  display: none;
  @include response(1200px) {
    display: block;
    flex: 0 0 auto;
  }
}
.filters-top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid var(--Line-Light, #eaeaea);
  background: var(--Bg-Light-gray, #f5f5f5);
  &__left {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__count {
    position: relative;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 20px;
    height: 20px;
    background: var(--Primary-Accent, #ff3d00);
    color: var(--Primary-White, #fff);
    font-size: 12px;
    line-height: 16px;
    border-radius: 50%;
    padding: 2px;
  }
}
.filters-top-count {
}
.filters-top-get-back {
  display: none;
  @include response(1200px) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__close {
    .level.filters-top-get-back & {
      display: none;
    }
  }
  &__arrow {
    display: none;
    .level.filters-top-get-back & {
      display: block;
    }
  }
}
.filters-top-name {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
}
.filters-top-reset {
  @include response(1200px) {
    color: var(--Primary-Accent, #ff3d00);
    font-size: 16px;
    line-height: 24px;
    display: none;
  }
  &.active {
    display: inline-block;
  }
  &--all {
  }
  &--single {
  }
}
.filters-wrapper {
  @include response(1200px) {
    flex: 1 1 auto;
    overflow-y: auto;
    overscroll-behavior: contain;
  }
}
.accordion {
  &__item {
  }
  &__handler {
  }
  &__slide {
  }
}
.filters-item {
  position: relative;
  border-bottom: 1px solid #eaeaea;
  padding: 16px;
  @include response(1200px) {
    &.active {
      border-bottom-color: transparent;
      .js-selected-items {
        display: none;
      }
    }
  }
  &__heading {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    cursor: pointer;
    @include desktop-hover() {
      > svg {
        stroke: $red;
      }
    }
    > svg {
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -8px;
      stroke: #000;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        transform: rotate(-90deg);
      }
    }
    .active.filters-item & {
      @include response-min(1201px) {
        > svg {
          transform: scaleY(-1);
        }
      }
    }
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
      .accordion__item.active & {
        > svg {
          display: none;
        }
      }
    }
    .js-filters-price-accordion & {
      @include response(1200px) {
        pointer-events: none;
        > svg {
          display: none;
        }
      }
    }
    .js-selected-items {
      display: none;
      @include response(1200px) {
        display: block;
        color: var(--Main-Gray, #595959);
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        padding-right: 60px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
  &__inner {
    padding-top: 16px;
    display: none;
    @include response(1200px) {
      display: none !important;
      .js-filters-price-accordion & {
        display: block !important;
      }
      .accordion__item.active & {
        display: block !important;
      }
    }
  }
  &__options {
    position: relative;
    max-height: 204px;
    overflow-y: auto;
    overscroll-behavior: contain;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #eaeaea;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #01231f;
      border-radius: 4px;
    }
  }
  &__option {
    display: flex;
    align-items: center;
    gap: 8px;
    position: relative;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    cursor: pointer;
    margin-bottom: 12px;
    transition: 0.3s ease-in-out;
    &:last-child {
      margin-bottom: 0;
    }
    input {
      display: none;
      &:checked {
        & + .color-filter-marker + .custom-checkbox__label,
        & + .custom-checkbox__label {
          &::before {
            background: $red;
            border-color: $red;
          }
        }
      }
    }
  }
  &__label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 60px;
    @include response(1200px) {
      .accordion__item.active & {
        padding-right: 0;
      }
    }
  }
  &__count {
    position: absolute;
    right: 28px;
    top: 50%;
    margin-top: -10px;
    display: none;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 50%;
    background: #ffede7;
    width: 20px;
    height: 20px;
    color: var(--Primary-Black, #000);
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 14px;
    &.active {
      display: flex;
    }
    @include response(1200px) {
      .accordion__item.active & {
        background: $red;
        color: #fff;
        right: 0;
      }
    }
  }
  &__input {
  }
}
.active {
}
.price-filter {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  column-gap: 8px;
  @include response(1200px) {
    column-gap: 0;
  }
  &--min {
    @include response(1200px) {
      border-radius: 4px 0px 0px 4px;
      border-right: 0 !important;
    }
  }
  &--max {
    @include response(1200px) {
      border-radius: 0px 4px 4px 0px;
    }
  }
}
.price-filter-input-wrapper {
  position: relative;
  flex: 0 0 calc(50% - 14px);
  width: calc(50% - 14px);
  @include response(1200px) {
    flex: 0 0 50%;
    width: 50%;
  }
}
.price-filter-input-label {
}
.price-filter-input {
  width: 100%;
  border: 1px solid #d3d3d3;
  background: #fff;
  padding: 7px 15px;
  color: var(--Primary-Black, #000);
  font-family: "Instrument Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  transition: 0.3s ease-in-out;
  @include response-min(1201px) {
    border-radius: 8px;
  }
  @include response(1200px) {
    font-weight: 700;
    text-align: center;
  }
  @include desktop-hover() {
    border-color: #bbb;
  }
  &:focus {
    border-color: #000;
  }
  &::placeholder {
    color: #969696;
    font-weight: 400;
  }
}
.price-filter-input-delimiter {
  position: relative;
  flex: 0 0 12px;
  width: 12px;
  height: 1px;
  background: #000;
  align-self: center;
  @include response(1200px) {
    display: none;
  }
}
.price-filter-btn {
  width: 100%;
  margin-top: 16px;
  padding-top: 9px;
  padding-bottom: 9px;
}
.custom-checkbox {
  position: relative;
  padding-left: 36px;
  &__label {
    display: inline-block;
    vertical-align: top;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -12px;
      width: 24px;
      height: 24px;
      border-radius: 4px;
      border: 1px solid var(--lines-light-gray-input, #cdcdcd);
      background: var(--background-bg-white-pages, #fff);
      transition: 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      width: 10px;
      height: 6px;
      border-bottom: 1px solid #fff;
      border-left: 1px solid #fff;
      top: 50%;
      margin-top: -5px;
      left: 7px;
      transform: rotate(-45deg);
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__label-extra {
  }
}
.filter-count {
  color: #454545;
  @include response(1200px) {
    margin-left: auto;
  }
}
.color-filter-marker {
  flex: 0 0 14px;
  width: 14px;
  height: 14px;
  border-radius: 2px;
}
.mobile-filter-buttons {
  display: none;
  @include response(1200px) {
    display: block;
    width: 100%;
    padding: 12px;
    background: #fff;
    border-top: 1px solid #eaeaea;
    flex: 0 0 auto;
  }
}
.save-filter {
  display: none;
  width: 100%;
  &.active {
    display: inline-flex;
  }
}
.apply-filter {
  display: none;
  width: 100%;
  &.active {
    display: inline-flex;
  }
}
