.footer {
  position: relative;
  background: var(--Primary-Main, #01231f);
  padding-top: 60px;
  padding-bottom: 60px;
  max-width: 100%;
  width: 100%;
  overflow: hidden;
  @include response(1200px) {
    padding-top: 32px;
    padding-bottom: 32px;
  }
  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.16);
    @include response(1200px) {
      padding-bottom: 32px;
      text-align: center;
      display: block;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 510px;
    width: 510px;
    @include response(1200px) {
      width: 100%;
      padding-bottom: 32px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.16);
      margin-bottom: 32px;
    }
  }
  &__logo {
    position: relative;
    display: inline-flex;
    vertical-align: top;
    margin-bottom: 16px;
    text-decoration: none;
  }
  &__text {
    position: relative;
    margin-bottom: 32px;
    color: #fff;
    line-height: 20px;
    @include response(1200px) {
      border-bottom: 1px solid rgba(255, 255, 255, 0.16);
      padding-bottom: 16px;
    }
  }
  &__social {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0 0 32px;
    display: flex;
    align-items: center;
    gap: 20px;
    @include response(1200px) {
      justify-content: center;
    }
    li {
      position: relative;
      flex: 0 0 auto;
      a {
        display: flex;
        transition: 0.1s ease-in-out;
        @include desktop-hover() {
          transform: translateY(-3px);
        }
      }
    }
  }
  &__join {
    position: relative;
    border-top: 1px solid rgba(255, 255, 255, 0.16);
    padding-top: 32px;
    @include response(1200px) {
      border-top: 0;
      padding-top: 0;
    }
  }
  &__join-title {
    position: relative;
    margin-bottom: 12px;
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__join-text {
    position: relative;
    margin-bottom: 12px;
    color: var(--main-30, #b3bdbc);
    line-height: 20px;
  }
  &__form {
    position: relative;
    display: flex;
    gap: 12px;
    @include response(767px) {
      display: block;
    }
    .input-style {
      margin: 0;
      flex: 1 1 auto;
      @include response(767px) {
        margin-bottom: 12px;
      }
    }
  }
  &__form-btn {
    flex: 0 0 auto;
    @include response(767px) {
      width: 100%;
    }
  }
  &__right {
    position: relative;
    padding-left: 40px;
    flex: 0 0 660px;
    width: 660px;
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    @include response(1200px) {
      width: 100%;
      padding-left: 0;
      gap: 32px;
      text-align: left;
    }
  }
  &__nav {
    position: relative;
    flex: 0 0 calc(50% - 20px);
    width: calc(50% - 20px);
    @include response(1200px) {
      flex: 0 0 calc(50% - 16px);
      width: calc(50% - 16px);
    }
  }
  &__nav-title {
    position: relative;
    margin-bottom: 16px;
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__nav-list {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      position: relative;
      margin-bottom: 16px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: block;
      text-decoration: none;
      color: #fff;
      line-height: 20px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: #ff3d00;
      }
    }
  }
  &__bottom {
    position: relative;
    padding-top: 20px;
    @include response(1200px) {
      padding-top: 32px;
      text-align: center;
    }
  }
  &__copy {
    color: var(--main-50, #80918f);
    line-height: 135%;
  }
}
