.category {
  position: relative;
  &__box {
    position: relative;
    display: flex;
    align-items: flex-start;
    gap: 20px;
    @include response(1200px) {
      display: block;
    }
  }
  &__filters {
    position: relative;
    flex: 0 0 240px;
    width: 240px;
    @include response(1200px) {
      width: 100%;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 100;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      transition: 0.3s ease-in-out;
      &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: initial;
      }
    }
  }
  &__holder {
    position: relative;
    flex: 1 1 auto;
    overflow: auto;
    @include response(1200px) {
      overflow: inherit;
    }
  }
  &__title {
    position: relative;
    margin: 0 0 16px;
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      font-size: 24px;
      line-height: 32px;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #eaeaea;
    @include response(1200px) {
      padding-bottom: 0;
      margin-bottom: 16px;
      border-bottom: 0;
    }
    p {
      margin: 0;
    }
    a {
      color: #000;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__text-inner {
    position: relative;
    @include response(1200px) {
      max-height: 72px;
      overflow: hidden;
      -webkit-line-clamp: 3;
      display: box;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      .category__text.active & {
        -webkit-line-clamp: initial;
        max-height: none;
      }
    }
  }
  &__text-mobile-btn {
    display: none;
    @include response(1200px) {
      display: inline-flex;
      align-items: center;
      font-weight: 500;
      gap: 8px;
      margin-top: 8px;
      span {
        &:nth-child(2) {
          display: none;
        }
      }
      .category__text.active & {
        svg {
          transform: scaleY(-1);
        }
        span {
          &:nth-child(1) {
            display: none;
          }
          &:nth-child(2) {
            display: block;
          }
        }
      }
    }
  }
  &__types {
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #eaeaea;
    @include response(1200px) {
      margin-bottom: 16px;
      padding-bottom: 16px;
      border-bottom: 0;
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: -16px;
        width: calc(100% + 32px);
        height: 1px;
        background: #eaeaea;
      }
    }
  }
  &__types-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    @include response(1200px) {
      flex-wrap: nowrap;
      overflow-x: auto;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
    }
    li {
      flex: 0 0 auto;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 12px;
      padding: 12px 16px;
      text-decoration: none;
      border-radius: 8px;
      background: #f5f5f5;
      color: var(--Primary-Black, #000);
      font-weight: 500;
      line-height: 20px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        background: $red;
        color: #fff;
        svg {
          stroke: #fff;
        }
      }
      svg {
        flex: 0 0 auto;
        stroke: #000;
        transition: 0.3s ease-in-out;
      }
    }
  }
  &__selected {
    padding-bottom: 20px;
    margin-bottom: 40px;
    border-bottom: 1px solid #eaeaea;
    @include response(1200px) {
      display: none;
    }
  }
  &__selected-head {
    position: relative;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    gap: 12px;
    @include response(1200px) {
      display: none;
    }
  }
  &__selected-title {
    position: relative;
    font-weight: 500;
    line-height: 20px;
  }
  &__selected-clear {
    position: relative;
    cursor: pointer;
    color: var(--Primary-Accent, #ff3d00);
    line-height: 24px;
    text-decoration: underline;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__selected-list {
    position: relative;
    display: flex;
    gap: 12px;
  }
  &__selected-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
    border-radius: 52px;
    border: 1px solid #d3d3d3;
    background: #fff;
    color: #000;
    padding: 8px 12px;
    cursor: pointer;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: $red;
      border-color: $red;
      color: #fff;
      svg {
        stroke: #fff;
      }
    }
  }
  &__selected-item-title {
    position: relative;
    flex: 0 0 auto;
  }
  &__selected-item-value {
    flex: 0 0 auto;
    font-weight: 500;
  }
  &__selected-item-remove {
    display: flex;
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__sort {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    @include response(767px) {
      flex-wrap: wrap;
    }
  }
  &__sort-text {
    color: #454545;
    line-height: 16px;
    @include response(1200px) {
      color: var(--Main-Gray, #595959);
      line-height: 20px;
    }
    @include response(767px) {
      padding-top: 16px;
      order: 3;
      width: 100%;
      margin-top: 16px;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        left: -16px;
        height: 1px;
        background: #eaeaea;
        width: calc(100% + 32px);
        top: 0;
      }
    }
  }
  &__sort-select {
    position: relative;
    flex: 0 0 206px;
    width: 206px;
    @include response(767px) {
      order: 2;
      width: calc(50% - 6px);
      flex: 0 0 calc(50% - 6px);
    }
  }
  &__list {
    position: relative;
    margin-bottom: 40px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__list-more {
    position: relative;
    padding: 16px;
    text-align: center;
  }
  &__list-info {
    position: relative;
    margin-bottom: 16px;
    color: #454545;
    line-height: 20px;
  }
  &__more-btn {
    position: relative;
  }
  &__mobile-filter-btn {
    position: relative;
    display: none;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 8px 16px;
    border-radius: 8px;
    background: var(--Primary-Main, #01231f);
    color: var(--Primary-White, #fff);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    align-self: stretch;
    @include response(1200px) {
      display: flex;
    }
    @include response(767px) {
      width: calc(50% - 6px);
      flex: 0 0 calc(50% - 6px);
      order: 1;
    }
    &--fixed {
      display: none;
      @include response(1200px) {
        position: fixed;
        bottom: 8px;
        left: 50%;
        display: flex;
        opacity: 0;
        visibility: hidden;
        transform: translate(-50%, 100%);
        width: auto;
        transition: .3s ease-in-out;
        &.visible {
          opacity: 1;
          visibility: visible;
          transform: translate(-50%, 0);
        }
      }
    }
  }
  &__mobile-filter-btn-counter {
    position: relative;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 20px;
    height: 20px;
    background: var(--Primary-Accent, #ff3d00);
    color: var(--Primary-White, #fff);
    font-size: 12px;
    line-height: 16px;
    border-radius: 50%;
    padding: 2px;
  }
}
