.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 10px 32px;
  border-radius: 8px;
  background: var(--Primary-Accent, #ff3d00);
  border: 2px solid #ff3d00;
  text-decoration: none;
  color: #fff;
  font-family: "Instrument Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  @include desktop-hover() {
    background: #fff;
    color: #ff3d00;
    svg {
      fill: #ff3d00;
    }
  }
  svg {
    fill: #fff;
    transition: 0.3s ease-in-out;
  }
  &--green {
    background: #4d6562;
    border-color: #4d6562;
    @include desktop-hover() {
      color: #4d6562;
      svg {
        fill: #4d6562;
      }
    }
  }
  &.disabled {
    background: #e6e9e9;
    border-color: #e6e9e9;
    pointer-events: none;
  }
}

.btn-black {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 8px 12px;
  border-radius: 8px;
  background: #01231f;
  border: 0;
  text-decoration: none;
  color: #fff;
  font-family: "Instrument Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  @include desktop-hover() {
    background: #ff3d00;
  }
  &.disabled {
    background: #e6e9e9;
    pointer-events: none;
  }
}

.wishlist-btn {
  position: relative;
  flex: 0 0 50px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 8px;
  border: 1px solid #d3d3d3;
  text-decoration: none;
  transition: 0.3s ease-in-out;
  &--small {
    flex: 0 0 36px;
    width: 36px;
    height: 36px;
    > svg {
      width: 16px;
      height: 16px;
    }
  }
  @include desktop-hover() {
    border-color: #ff3d00;
    .wishlist-btn__border,
    .wishlist-btn__inner {
      fill: #ff3d00;
    }
  }
  &__border {
    fill: #000;
    transition: 0.3s ease-in-out;
    .wishlist-btn.active & {
      fill: #000;
    }
  }
  &__inner {
    fill: #fff;
    transition: 0.3s ease-in-out;
    .wishlist-btn.active & {
      fill: #000;
    }
  }
}

.btn-small {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: 7px 32px;
  border-radius: 4px;
  background: var(--Primary-Accent, #ff3d00);
  border: 1px solid #ff3d00;
  text-decoration: none;
  color: #fff;
  font-family: "Instrument Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  &.disabled {
    background: #e6e9e9;
    border-color: #e6e9e9;
    pointer-events: none;
  }
  @include response(767px) {
    padding-left: 12px;
    padding-right: 12px;
  }
  @include desktop-hover() {
    background: #fff;
    color: #ff3d00;
    svg {
      fill: #ff3d00;
    }
  }
  svg {
    fill: #fff;
    transition: 0.3s ease-in-out;
  }
  &--green {
    background: #4d6562;
    border-color: #4d6562;
    @include desktop-hover() {
      color: #4d6562;
      svg {
        fill: #4d6562;
      }
    }
  }
}

.btn-white {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 11px 31px;
  border-radius: 8px;
  border: 1px solid #d3d3d3;
  background: var(--Primary-White, #fff);
  text-decoration: none;
  color: var(--Primary-Black, #000);
  font-family: "Instrument Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  transition: 0.3s ease-in-out;
  @include desktop-hover() {
    border-color: $red;
    color: $red;
  }
  &.disabled {
    background: #e6e9e9;
    border-color: #e6e9e9;
    pointer-events: none;
    color: #fff;
  }
}

.btn-scroll-top {
  display: none;
  @include response(1200px) {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    right: 8px;
    bottom: 8px;
    width: 40px;
    height: 40px;
    border-radius: 4px;
    background: var(--Primary-Main, #01231f);
    opacity: 0;
    visibility: hidden;
    transform: translateY(100%);
    transition: 0.3s ease-in-out;
    &.scroll {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
}
