.product {
  position: relative;
  &__box {
    position: relative;
    display: flex;
    align-items: flex-start;
    padding-bottom: 40px;
    @include response(1200px) {
      display: block;
      padding-bottom: 24px;
    }
  }
  &__sliders-box {
    position: sticky;
    top: 10px;
    flex: 0 0 634px;
    width: 634px;
    display: flex;
    gap: 24px;
    margin-right: 64px;
    @include response(1200px) {
      position: relative;
      width: 100%;
      display: block;
      margin-right: 0;
      top: auto;
      padding-bottom: 40px;
    }
  }
  &__sliders-button {
    position: absolute;
    left: 0;
    width: 70px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    cursor: pointer;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      svg {
        stroke: #ff3d00;
      }
    }
    &--prev {
      top: 0;
    }
    &--next {
      bottom: 16px;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      svg {
        stroke: #b3bdbc;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &::after {
      display: none;
    }
  }
  &__main-zoom-btn {
    position: absolute;
    bottom: 16px;
    right: 16px;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid #d3d3d3;
    background: #fff;
    z-index: 2;
    pointer-events: none;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: 26px;
      height: 26px;
    }
    svg {
      fill: #000;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        width: 16px;
        height: 16px;
      }
    }
  }
  &__thumbnails-slider {
    flex: 0 0 70px;
    width: 70px;
    max-width: 70px;
    height: 460px;
    margin-top: 32px;
    @include response(1200px) {
      display: none !important;
    }
  }
  &__thumbnails-slide {
    cursor: pointer;
    @include desktop-hover() {
      &::before {
        border-color: #ff3d00;
      }
    }
    &.swiper-slide-thumb-active {
      &::before {
        border-color: #ff3d00;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      border: 1px solid transparent;
      transition: 0.3s ease-in-out;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__main-slider {
    flex: 0 0 540px;
    width: 540px;
    max-width: 540px;
    height: 540px;
    border: 1px solid #eaeaea;
    @include response(1200px) {
      width: 100%;
      height: auto;
    }
    @include desktop-hover() {
      .product__main-zoom-btn {
        background: #ff3d00;
        border-color: #ff3d00;
        svg {
          fill: #fff;
        }
      }
    }
  }
  &__main-slider-pagination {
    display: none;
    @include response(1200px) {
      display: flex;
      justify-content: center;
      position: absolute;
      bottom: 16px !important;
    }
  }
  &__main-slide {
    position: relative;
  }
  &__info {
    position: relative;
    max-width: 472px;
    width: 100%;
    flex: 0 0 472px;
  }
  &__title-box {
    position: relative;
    margin-bottom: 12px;
    display: flex;
    align-items: flex-start;
    gap: 16px;
  }
  &__title-box-left {
    position: relative;
  }
  &__title-box-label {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 4px;
    color: var(--Primary-Accent, #ff3d00);
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-decoration: none;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__title-box-title {
    position: relative;
    margin: 0;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
  }
  &__title-box-right {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
  }
  &__reviews {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    gap: 8px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__reviews-value {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    flex: 0 0 auto;
  }
  &__reviews-rate {
    position: relative;
    flex: 0 0 auto;
  }
  &__reviews-info {
    position: relative;
    flex: 0 0 auto;
    color: var(--Primary-Accent, #ff3d00);
    line-height: 24px;
    text-decoration-line: underline;
  }
  &__tags {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__tags-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }
  &__tags-item {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 12px;
    border-radius: 8px;
    background: #f5f5f5;
    font-weight: 500;
    line-height: 16px;
    img {
      flex: 0 0 16px;
      width: 16px;
    }
  }
  &__tags-hint {
    position: relative;
    flex: 0 0 auto;
    line-height: 24px;
    text-decoration-line: underline;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: #ff3d00;
    }
  }
  &__discount {
    position: relative;
    padding-top: 16px;
    padding-bottom: 16px;
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    @include response(767px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__discount-left {
    position: relative;
  }
  &__discount-text {
    margin-bottom: 2px;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
  }
  &__discount-box {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__discount-title {
    color: #454545;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
  &__discount-value {
    color: var(--Primary-Accent, #ff3d00);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    &:not(.copy) {
      filter: blur(3px);
    }
  }
  &__discount-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: var(--Primary-White, #fff);
    transition: 0.3s ease-in-out;
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    @include desktop-hover() {
      background: #ff3d00;
      border-color: #ff3d00;
    }
    .copied {
      display: none;
    }
    &.copy {
      .copy {
        display: block;
      }
    }
    &.copied {
      background: #ebf8f7;
      border-color: #ebf8f7;
      cursor: default;
      svg {
        stroke: none;
      }
      .copied {
        display: block;
      }
      .copy {
        display: none;
      }
    }
  }
  &__select {
    padding-top: 24px;
    padding-bottom: 24px;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
      padding-top: 16px;
      padding-bottom: 16px;
    }
  }
  &__labels {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 12px;
  }
  &__labels-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 4px 12px;
    border-radius: 54px;
    background: #ebf8f7;
    color: #08584e;
    font-size: 16px;
    line-height: 24px;
  }
  &__price {
    position: relative;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price-left {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    @include response(767px) {
      margin-bottom: 10px;
    }
  }
  &__price-stock {
    position: relative;
  }
  &__price-stock-label {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
    margin-bottom: 4px;
    color: var(--main-70, #4d6562);
    font-weight: 500;
    line-height: 20px;
  }
  &__price-value {
    position: relative;
    color: #f70808;
    font-size: 32px;
    font-weight: 700;
    line-height: 38px;
    &--stock {
      color: #4d6562;
    }
  }
  &__price-box {
    position: relative;
  }
  &__price-old {
    position: relative;
    color: var(--main-50, #80918f);
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    text-decoration: line-through;
  }
  &__price-save {
    position: relative;
    color: #f70808;
    font-size: 10px;
    font-weight: 700;
    line-height: 14px;
    text-transform: uppercase;
  }
  &__price-right {
    position: relative;
  }
  &__price-ready {
    display: flex;
    align-items: center;
    gap: 4px;
    color: #0d9a88;
    font-weight: 500;
    line-height: 20px;
  }
  &__lead-time {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__lead-time-title {
    color: #454545;
    line-height: 16px;
  }
  &__lead-time-value {
    color: #454545;
    line-height: 16px;
    font-weight: 700;
  }
  &__lead-time-info {
    display: flex;
    flex: 0 0 auto;
  }
  &__credit {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  &__credit-title {
    flex: 0 0 auto;
    line-height: 16px;
  }
  &__credit-icon {
    position: relative;
    display: flex;
    flex: 0 0 auto;
  }
  &__credit-btn {
    position: relative;
    flex: 0 0 auto;
    cursor: pointer;
    text-decoration: underline;
    color: var(--Color-Black, #000);
    line-height: 18px;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__controls {
    position: relative;
    display: flex;
    margin-bottom: 24px;
    gap: 12px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__wishlist-btn {
    position: relative;
  }
  &__add-btn-box {
    position: relative;
    flex: 1 1 auto;
  }
  &__add-btn {
    width: 100%;
  }
  &__additional-info {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    gap: 8px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
    @include response(767px) {
      display: block;
    }
  }
  &__additional-info-item {
    position: relative;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    background: #f5f5f5;
    padding: 12px 16px;
    cursor: pointer;
    @include response(767px) {
      margin-bottom: 8px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    @include desktop-hover() {
      .product__additional-info-title {
        color: #ff3d00;
      }
    }
  }
  &__additional-info-icon {
    flex: 0 0 24px;
  }
  &__additional-info-box {
    position: relative;
  }
  &__additional-info-title {
    font-weight: 500;
    line-height: 20px;
    margin-bottom: 2px;
    transition: 0.3s ease-in-out;
  }
  &__additional-info-text {
    color: #454545;
    line-height: 16px;
  }
  &__additional-info-arrow {
    flex: 0 0 16px;
  }
  &__mentioned-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }
  &__mentioned-item {
    position: relative;
    text-decoration: none;
    border-radius: 52px;
    border: 1px solid #d3d3d3;
    background: #fff;
    padding: 8px 12px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: #000;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #ff3d00;
      color: #fff;
      background: #ff3d00;
    }
  }
  &__no-availability-info {
    position: relative;
    text-align: center;
    line-height: 16px;
    margin-top: 12px;
  }
  &__reviews-section {
    position: relative;
    padding-top: 40px;
    padding-bottom: 40px;
    @include response(1200px) {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }
  &__reviews-section-title {
    position: relative;
    margin: 0 0 16px;
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
  }
  &__reviews-section-text {
    position: relative;
    margin-bottom: 24px;
    color: #454545;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
  &__reviews-section-add {
    position: relative;
  }
  &__reviews-section-holder {
    position: relative;
  }
}
