html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  margin: 0;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0;
}

input::-ms-clear {
  display: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

button,
input[type=submit] {
  cursor: pointer;
}

:root {
  --Primary-Main: #01231f;
  --main-30: #b3bdbc;
  --Primary-White: #fff;
  --background-bg-white-pages: #fff;
  --main-50: #80918f;
  --Primary-Accent: #ff3d00;
  --Primary-Black: #000;
  --Mask-Black---30: rgba(0, 0, 0, 0.3);
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
  font-family: "Instrument Sans", serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: #000;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
}
body.ovh {
  overflow: hidden;
}
@media only screen and (max-width: 1200px) {
  body ::-webkit-scrollbar {
    display: none;
  }
}

.wrapper {
  position: relative;
  flex: 1 1 auto;
  padding-bottom: 40px;
}
@media only screen and (max-width: 1200px) {
  .wrapper {
    max-width: 100%;
    width: 100%;
    overflow: hidden;
    padding-bottom: 20px;
  }
}

.container {
  max-width: 1312px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

.title {
  position: relative;
  font-family: "Instrument Sans";
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 36px;
  margin: 0;
}
@media only screen and (max-width: 1200px) {
  .title {
    font-size: 24px;
  }
}

* {
  outline: none;
  box-sizing: border-box;
}
*::after, *::before {
  box-sizing: border-box;
}

.accordion {
  position: relative;
}
.accordion__item {
  position: relative;
}
.accordion__item-head {
  position: relative;
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  border-top: 1px solid #eaeaea;
  cursor: pointer;
}
@media only screen and (min-width: 1025px) {
  .accordion__item-head:hover svg {
    stroke: #ff3d00;
  }
}
.accordion__item.active .accordion__item-head svg {
  transform: scaleY(-1);
}
.accordion__item-head svg {
  stroke: #000;
  transition: 0.3s ease-in-out;
}
.accordion__item-body {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
  overflow: hidden;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  white-space: normal;
  display: none;
}
@media only screen and (max-width: 767px) {
  .accordion__item-body {
    margin-left: -16px;
    margin-right: -16px;
    padding-left: 16px;
    padding-right: 16px;
  }
}
.accordion__item.full .accordion__item-body {
  -webkit-line-clamp: none !important;
}
.accordion__item.active .accordion__item-body {
  display: box;
  display: -webkit-box;
}
.accordion__item-body p {
  margin: 0 0 20px;
}
.accordion__item-body ul {
  padding: 0 0 0 25px;
  margin: 0 0 20px;
}
.accordion__item-body ul:last-child {
  margin: 0;
}
.accordion__item-body ul li {
  margin-bottom: 8px;
}
.accordion__item-body ul li span {
  font-weight: 500;
}
.accordion__item-body ul li:last-child {
  margin-bottom: 0;
}
.accordion__item-btn {
  position: relative;
  gap: 8px;
  align-items: center;
  justify-content: center;
  color: var(--Primary-Main, #01231f);
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  text-decoration: underline;
  cursor: pointer;
  display: none;
  padding-bottom: 16px;
}
@media only screen and (min-width: 1025px) {
  .accordion__item-btn:hover {
    text-decoration: none;
  }
}
.accordion__item-btn svg {
  transition: 0.3s ease-in-out;
}
.accordion__item-btn span:nth-child(1) {
  display: block;
}
.accordion__item-btn span:nth-child(2) {
  display: none;
}
.accordion__item.full .accordion__item-btn svg {
  transform: scaleY(-1);
}
.accordion__item.full .accordion__item-btn span:nth-child(1) {
  display: none;
}
.accordion__item.full .accordion__item-btn span:nth-child(2) {
  display: block;
}
.accordion__item.active .accordion__item-btn {
  display: inline-flex;
}

.handling-shipping-row {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
  color: #000;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.text-grey {
  position: relative;
  font-size: 16px;
  line-height: 20px;
  padding: 8px 12px;
  border-radius: 4px;
  background: #f5f5f5;
}
@media only screen and (max-width: 767px) {
  .text-grey {
    margin-left: -16px !important;
    margin-right: -16px !important;
    padding: 16px;
    border-radius: 0;
  }
}

.link-return-policy {
  display: inline-flex;
  align-items: center;
  vertical-align: top;
  text-decoration: none;
  color: var(--Primary-Main, #01231f);
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  gap: 4px;
}
@media only screen and (min-width: 1025px) {
  .link-return-policy:hover {
    text-decoration: underline;
  }
}
.link-return-policy span {
  color: #ff3d00;
}
.link-return-policy svg {
  stroke: #ff3d00;
}

.wpcf7-not-valid-tip {
  position: absolute !important;
  left: 0 !important;
  right: 0 !important;
  top: 100%;
  margin: 4px 0 0 !important;
  color: #f70808 !important;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.wpcf7-form-control-wrap {
  display: block;
}

.wpcf7 form .wpcf7-response-output {
  display: none !important;
}

.wpcf7-form-control-wrap {
  position: relative;
  display: block;
}
.wpcf7-form-control-wrap span {
  display: block;
  position: relative;
  margin: 0;
}
.wpcf7-form-control-wrap label {
  display: block;
}
.wpcf7-form-control-wrap label input {
  display: none;
}
.wpcf7-form-control-wrap label input:checked + span::after {
  opacity: 1;
}
.wpcf7-form-control-wrap label span {
  position: relative;
  display: block;
  padding-left: 25px;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  color: #9b9b9b;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.wpcf7-form-control-wrap label span::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 1px;
  width: 12px;
  height: 12px;
  background: #fff;
}
.wpcf7-form-control-wrap label span::after {
  content: "";
  position: absolute;
  left: 5px;
  top: 3px;
  width: 8px;
  height: 5px;
  border-left: 2px solid #000;
  border-bottom: 2px solid #000;
  transform: rotate(-45deg);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.wpcf7-form-control-wrap label span a {
  color: #9b9b9b;
}
@media only screen and (min-width: 1025px) {
  .wpcf7-form-control-wrap label span a:hover {
    text-decoration: none;
  }
}

.error {
  display: none;
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  margin: 0;
  color: #fff;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  border-radius: 8px;
  background: #f70808;
  padding: 8px 12px;
  z-index: 2;
}
@media only screen and (max-width: 1200px) {
  .error {
    top: 100%;
    width: 100%;
    border-radius: 0;
  }
}
.error.show {
  display: block;
}
.error::before {
  content: "";
  position: absolute;
  border: 6px solid transparent;
  border-bottom: 4px solid #f70808;
  bottom: 100%;
  left: 24px;
  right: auto;
  top: auto;
  margin-top: 0;
}

.breadcrumbs {
  list-style: none;
  padding: 8px 0;
  margin: 0 0 30px;
  display: flex;
  overflow-x: auto;
  gap: 32px;
}
@media only screen and (max-width: 1200px) {
  .breadcrumbs {
    gap: 20px;
    margin-left: -16px;
    margin-right: -16px;
    padding-left: 16px;
    padding-right: 16px;
    margin-bottom: 24px;
  }
}
.breadcrumbs li {
  flex: 0 0 auto;
}
.breadcrumbs li a {
  display: block;
  text-decoration: none;
  position: relative;
  color: #454545;
  line-height: 16px;
  transition: 0.3s ease-in-out;
}
@media only screen and (min-width: 1025px) {
  .breadcrumbs li a:hover {
    color: #ff3d00;
  }
}
.breadcrumbs li a::before {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -4px;
  right: -19px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 0 4px 6px;
  border-color: transparent transparent transparent #b3bdbc;
  transform: rotate(0deg);
}
@media only screen and (max-width: 1200px) {
  .breadcrumbs li a::before {
    right: -14px;
  }
}
.breadcrumbs li span {
  display: block;
  color: #454545;
  line-height: 16px;
}

.btn {
