.category {
  position: relative;
}
.category__box {
  position: relative;
  display: flex;
  align-items: flex-start;
  gap: 20px;
}
@media only screen and (max-width: 1200px) {
  .category__box {
    display: block;
  }
}
.category__filters {
  position: relative;
  flex: 0 0 240px;
  width: 240px;
}
@media only screen and (max-width: 1200px) {
  .category__filters {
    width: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: 0.3s ease-in-out;
  }
  .category__filters.active {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
  }
}
.category__holder {
  position: relative;
  flex: 1 1 auto;
  overflow: auto;
}
@media only screen and (max-width: 1200px) {
  .category__holder {
    overflow: inherit;
  }
}
.category__title {
  position: relative;
  margin: 0 0 16px;
  font-size: 28px;
  font-weight: 500;
  line-height: 36px;
}
@media only screen and (max-width: 1200px) {
