    font-size: 24px;
    line-height: 32px;
  }
}
.category__text {
  font-size: 16px;
  line-height: 24px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #eaeaea;
}
@media only screen and (max-width: 1200px) {
