    padding-bottom: 0;
    margin-bottom: 16px;
    border-bottom: 0;
  }
}
.category__text p {
  margin: 0;
}
.category__text a {
  color: #000;
}
@media only screen and (min-width: 1025px) {
  .category__text a:hover {
    text-decoration: none;
  }
}
.category__text-inner {
  position: relative;
}
@media only screen and (max-width: 1200px) {
  .category__text-inner {
    max-height: 72px;
    overflow: hidden;
    -webkit-line-clamp: 3;
    display: box;
    display: -webkit-box;
