.tooltip {
  position: absolute;
  padding: 8px 12px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 4px;
  font-size: 14px;
  white-space: nowrap;
  z-index: 1000;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.tooltip.visible {
  opacity: 1;
}

.wishlist {
  position: relative;
}
.wishlist__head {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  gap: 16px;
}
@media only screen and (max-width: 767px) {
  .wishlist__head {
    display: block;
  }
}
.wishlist__title {
  position: relative;
}
@media only screen and (max-width: 767px) {
  .wishlist__title {
    margin-bottom: 32px;
  }
}
.wishlist__filters {
  position: relative;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  gap: 16px;
}
@media only screen and (max-width: 767px) {
  .wishlist__filters {
    gap: 12px;
  }
}
.wishlist__filters-select {
  position: relative;
  flex: 0 0 auto;
}
@media only screen and (max-width: 767px) {
  .wishlist__filters-select {
    width: calc(50% - 6px);
    flex: 0 0 calc(50% - 6px);
  }
}
.wishlist__body {
  position: relative;
}
.wishlist__list {
  position: relative;
  margin-bottom: 32px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
@media only screen and (max-width: 1200px) {
  .wishlist__list {
    overflow-x: auto;
    margin-left: -16px;
    margin-right: -16px;
    padding-left: 16px;
    padding-right: 16px;
    flex-wrap: nowrap;
    gap: 8px;
  }
}
.wishlist__item {
  position: relative;
  flex: 0 0 calc(25% - 15px);
  width: calc(25% - 15px);
  border-radius: 2px;
  border: 1px solid #eaeaea;
  background: var(--Primary-White, #fff);
}
@media only screen and (max-width: 1200px) {
  .wishlist__item {
    flex: 0 0 calc(33.333% - 5px);
    width: calc(33.333% - 5px);
  }
}
@media only screen and (max-width: 767px) {
  .wishlist__item {
    flex: 0 0 calc(50% - 4px);
    width: calc(50% - 4px);
  }
}
.wishlist__item-img {
  position: relative;
  display: block;
  text-decoration: none;
  overflow: hidden;
}
.wishlist__item-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.wishlist__item-delete {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 36px;
  height: 36px;
  border-radius: 4px;
  border: 1px solid #d3d3d3;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  transition: 0.3s ease-in-out;
}
@media only screen and (max-width: 767px) {
  .wishlist__item-delete {
    top: 8px;
    right: 8px;
    width: 32px;
    height: 32px;
  }
}
@media only screen and (min-width: 1025px) {
  .wishlist__item-delete:hover {
    border-color: #ff3d00;
    background: #ff3d00;
  }
  .wishlist__item-delete:hover svg {
    stroke: #fff;
  }
}
.wishlist__item-delete svg {
  stroke: #4d6562;
  transition: 0.3s ease-in-out;
}
.wishlist__item-body {
  position: relative;
  padding: 8px;
}
.wishlist__item-title-box {
  position: relative;
  display: block;
  text-decoration: none;
  color: var(--Primary-Black, #000);
  margin-bottom: 8px;
}
@media only screen and (min-width: 1025px) {
  .wishlist__item-title-box:hover {
    text-decoration: underline;
  }
}
.wishlist__item-category {
  display: block;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.wishlist__item-title {
  display: block;
  color: #454545;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  position: relative;
  height: 40px;
  overflow: hidden;
  -webkit-line-clamp: 2;
  display: box;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.wishlist__item-row {
  position: relative;
  padding-top: 8px;
  padding-bottom: 8px;
  border-top: 1px solid #eaeaea;
  display: flex;
  align-items: center;
  gap: 8px;
}
@media only screen and (max-width: 767px) {
  .wishlist__item-row {
    display: block;
  }
}
.wishlist__item-row--block {
  display: block;
}
.wishlist__item-rate {
  position: relative;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 4px;
}
.wishlist__item-rate-count {
  color: #646464;
  line-height: 16px;
}
.wishlist__item-price {
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}
.wishlist__item-row-title {
  line-height: 20px;
  flex: 0 0 auto;
  max-width: 50%;
  overflow: auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media only screen and (max-width: 767px) {
  .wishlist__item-row-title {
    max-width: 100%;
  }
}
.wishlist__item-row-val {
  position: relative;
  overflow: auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 500;
  line-height: 20px;
}
.wishlist__select {
  position: relative;
  border-top: 1px solid #eaeaea;
  padding-top: 8px;
  padding-bottom: 8px;
}
.wishlist__item-add {
  width: 100%;
}
.wishlist__specifications {
  position: relative;
  flex: 0 0 auto;
}
@media only screen and (max-width: 767px) {
  .wishlist__specifications {
    width: calc(50% - 6px);
    flex: 0 0 calc(50% - 6px);
  }
}
.wishlist__specifications-btn {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 11px 15px;
  border-radius: 8px;
  border: 1px solid var(--Primary-Main, #01231f);
  background: #01231f;
  color: #fff;
  font-family: "Instrument Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  transition: 0.3s ease-in-out;
}
@media only screen and (min-width: 1025px) {
  .wishlist__specifications-btn:hover {
    background: #fff;
    color: var(--Primary-Black, #000);
  }
  .wishlist__specifications-btn:hover svg {
    fill: #01231f;
  }
}
.wishlist__specifications.active .wishlist__specifications-btn {
  background: #fff;
  color: var(--Primary-Black, #000);
}
.wishlist__specifications.active .wishlist__specifications-btn svg {
  fill: #01231f;
}
.wishlist__specifications-btn svg {
  fill: #fff;
  transition: 0.3s ease-in-out;
}
.wishlist__checkboxes {
  position: absolute;
  top: 100%;
  left: 0;
  border-radius: 8px;
  background: var(--background-bg-white-pages, #fff);
  box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
  width: 220px;
  padding: 8px 16px;
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease-in-out;
}
.wishlist__checkboxes.right {
  left: auto;
  right: 0;
}
.wishlist__checkboxes.bottom {
  bottom: 100%;
  top: auto;
}
.wishlist__specifications.active .wishlist__checkboxes {
  opacity: 1;
  visibility: visible;
}
.wishlist__checkboxes-box {
  max-height: 276px;
  overflow: auto;
  overscroll-behavior: contain;
}
.wishlist__checkboxes-box::-webkit-scrollbar {
  width: 3px;
}
.wishlist__checkboxes-box::-webkit-scrollbar-track {
  background: #eaeaea;
}
.wishlist__checkboxes-box::-webkit-scrollbar-thumb {
  background-color: #01231f;
  border-radius: 4px;
}
.wishlist__checkboxes-item {
  position: relative;
  display: block;
  margin-bottom: 12px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
@media only screen and (min-width: 1025px) {
  .wishlist__checkboxes-item:hover {
    color: #ff3d00;
  }
}
.wishlist__checkboxes-item span {
  display: block;
  position: relative;
  padding-left: 36px;
  line-height: 24px;
}
.wishlist__checkboxes-item span::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 4px;
  border: 1px solid var(--lines-light-gray-input, #cdcdcd);
  background: var(--background-bg-white-pages, #fff);
  width: 24px;
  height: 24px;
  transition: 0.3s ease-in-out;
}
.wishlist__checkboxes-item span::after {
  content: "";
  position: absolute;
  left: 7px;
  top: 7px;
  width: 10px;
  height: 6px;
  border-bottom: 1px solid #fff;
  border-left: 1px solid #fff;
  transform: rotate(-45deg);
}
.wishlist__checkboxes-item input {
  display: none;
}
.wishlist__checkboxes-item input:checked + span::before {
  background: #ff3d00;
  border-color: #ff3d00;
}
.wishlist__checkboxes-item:last-child {
  margin-bottom: 0;
}
.wishlist__specifications-counter {
  position: relative;
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  color: var(--White, #fff);
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ff3d00;
}