.wpcf7-not-valid-tip {
  position: absolute !important;
  left: 0 !important;
  right: 0 !important;
  top: 100%;
  margin: 4px 0 0 !important;
  color: #f70808 !important;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}
.wpcf7-form-control-wrap {
  display: block;
}
.wpcf7 form .wpcf7-response-output {
  display: none !important;
}

.wpcf7-form-control-wrap {
  position: relative;
  display: block;
  span {
    display: block;
    position: relative;
    margin: 0;
  }
  label {
    display: block;
    input {
      display: none;
      &:checked {
        + span {
          &::after {
            opacity: 1;
          }
        }
      }
    }
    span {
      position: relative;
      display: block;
      padding-left: 25px;
      font-weight: 300;
      font-size: 12px;
      line-height: 14px;
      color: #9b9b9b;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      &::before {
        content: "";
        position: absolute;
        left: 3px;
        top: 1px;
        width: 12px;
        height: 12px;
        background: #fff;
      }
      &::after {
        content: "";
        position: absolute;
        left: 5px;
        top: 3px;
        width: 8px;
        height: 5px;
        border-left: 2px solid #000;
        border-bottom: 2px solid #000;
        transform: rotate(-45deg);
        opacity: 0;
        transition: all 0.3s ease-in-out;
      }
      a {
        color: #9b9b9b;
        @include desktop-hover() {
          text-decoration: none;
        }
      }
    }
  }
}

.error {
  display: none;
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  margin: 0;
  color: #fff;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  border-radius: 8px;
  background: #f70808;
  padding: 8px 12px;
  z-index: 2;
  @include response(1200px) {
    top: 100%;
    width: 100%;
    border-radius: 0;
  }
  &.show {
    display: block;
  }
  &::before {
    content: "";
    position: absolute;
    border: 6px solid transparent;
    border-bottom: 4px solid #f70808;
    bottom: 100%;
    left: 24px;
    right: auto;
    top: auto;
    margin-top: 0;
  }
}
