.wishlist {
  position: relative;
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 32px;
    gap: 16px;
    @include response(767px) {
      display: block;
    }
  }
  &__title {
    position: relative;
    @include response(767px) {
      margin-bottom: 32px;
    }
  }
  &__filters {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    gap: 16px;
    @include response(767px) {
      gap: 12px;
    }
  }
  &__filters-select {
    position: relative;
    flex: 0 0 auto;
    @include response(767px) {
      width: calc(50% - 6px);
      flex: 0 0 calc(50% - 6px);
    }
  }
  &__body {
    position: relative;
  }
  &__list {
    position: relative;
    margin-bottom: 32px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    @include response(1200px) {
      overflow-x: auto;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
      flex-wrap: nowrap;
      gap: 8px;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(25% - 15px);
    width: calc(25% - 15px);
    border-radius: 2px;
    border: 1px solid #eaeaea;
    background: var(--Primary-White, #fff);
    @include response(1200px) {
      flex: 0 0 calc(33.333% - 5px);
      width: calc(33.333% - 5px);
    }
    @include response(767px) {
      flex: 0 0 calc(50% - 4px);
      width: calc(50% - 4px);
    }
  }
  &__item-img {
    position: relative;
    display: block;
    text-decoration: none;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-delete {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 36px;
    height: 36px;
    border-radius: 4px;
    border: 1px solid #d3d3d3;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    transition: 0.3s ease-in-out;
    @include response(767px) {
      top: 8px;
      right: 8px;
      width: 32px;
      height: 32px;
    }
    @include desktop-hover() {
      border-color: $red;
      background: $red;
      svg {
        stroke: #fff;
      }
    }
    svg {
      stroke: #4d6562;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-body {
    position: relative;
    padding: 8px;
  }
  &__item-title-box {
    position: relative;
    display: block;
    text-decoration: none;
    color: var(--Primary-Black, #000);
    margin-bottom: 8px;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__item-category {
    display: block;
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__item-title {
    display: block;
    color: #454545;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    position: relative;
    height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  &__item-row {
    position: relative;
    padding-top: 8px;
    padding-bottom: 8px;
    border-top: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    gap: 8px;
    @include response(767px) {
      display: block;
    }
    &--block {
      display: block;
    }
  }
  &__item-rate {
    position: relative;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__item-rate-count {
    color: #646464;
    line-height: 16px;
  }
  &__item-price {
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
  }
  &__item-row-title {
    line-height: 20px;
    flex: 0 0 auto;
    max-width: 50%;
    overflow: auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    @include response(767px) {
      max-width: 100%;
    }
  }
  &__item-row-val {
    position: relative;
    overflow: auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 500;
    line-height: 20px;
  }
  &__select {
    position: relative;
    border-top: 1px solid #eaeaea;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  &__item-add {
    width: 100%;
  }
  &__specifications {
    position: relative;
    flex: 0 0 auto;
    @include response(767px) {
      width: calc(50% - 6px);
      flex: 0 0 calc(50% - 6px);
    }
  }
  &__specifications-btn {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 11px 15px;
    border-radius: 8px;
    border: 1px solid var(--Primary-Main, #01231f);
    background: #01231f;
    color: #fff;
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #fff;
      color: var(--Primary-Black, #000);
      svg {
        fill: #01231f;
      }
    }
    .wishlist__specifications.active & {
      background: #fff;
      color: var(--Primary-Black, #000);
      svg {
        fill: #01231f;
      }
    }
    svg {
      fill: #fff;
      transition: 0.3s ease-in-out;
    }
  }
  &__checkboxes {
    position: absolute;
    top: 100%;
    left: 0;
    border-radius: 8px;
    background: var(--background-bg-white-pages, #fff);
    box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
    width: 220px;
    padding: 8px 16px;
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    &.right {
      left: auto;
      right: 0;
    }
    &.bottom {
      bottom: 100%;
      top: auto;
    }
    .wishlist__specifications.active & {
      opacity: 1;
      visibility: visible;
    }
  }
  &__checkboxes-box {
    max-height: 276px;
    overflow: auto;
    overscroll-behavior: contain;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #eaeaea;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #01231f;
      border-radius: 4px;
    }
  }
  &__checkboxes-item {
    position: relative;
    display: block;
    margin-bottom: 12px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
    span {
      display: block;
      position: relative;
      padding-left: 36px;
      line-height: 24px;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 4px;
        border: 1px solid var(--lines-light-gray-input, #cdcdcd);
        background: var(--background-bg-white-pages, #fff);
        width: 24px;
        height: 24px;
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        left: 7px;
        top: 7px;
        width: 10px;
        height: 6px;
        border-bottom: 1px solid #fff;
        border-left: 1px solid #fff;
        transform: rotate(-45deg);
      }
    }
    input {
      display: none;
      &:checked {
        & + span {
          &::before {
            background: $red;
            border-color: $red;
          }
        }
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__specifications-counter {
    position: relative;
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: var(--White, #fff);
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $red;
  }
}
