.info {
  display: flex;
  position: relative;
  svg {
    fill: #80918f;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  &__hint {
    position: absolute;
    padding: 15px;
    font-size: 14px;
    line-height: 1.4;
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    width: 320px;
    border-radius: 4px;
    font-size: 14px;
    z-index: 1000;
    display: none;
    @include response(767px) {
      width: 250px;
    }
    p {
      margin: 0;
    }
  }
  &__arrow {
    position: absolute;
    --tw-bg-opacity: 1;
    background: rgb(24 25 31 / var(--tw-bg-opacity));
    // background: #222;
    width: 12px;
    height: 12px;
    transform: rotate(45deg);
  }
}
