.header {
  position: relative;
  z-index: 2;
  &__label {
    position: relative;
    background: #4d6562;
    padding: 10px 16px;
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    @include response(1200px) {
      padding-top: 5px;
      padding-bottom: 5px;
    }
    a {
      color: #fff;
      font-weight: 700;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
    span {
      font-weight: 700;
    }
  }
  &__top {
    position: relative;
    background: var(--Primary-Main, #01231f);
    padding-top: 12px;
    padding-bottom: 12px;
  }
  &__top-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left {
    display: flex;
    align-items: center;
    @include response(1200px) {
      gap: 24px;
    }
  }
  &__menu-btn {
    display: none;
    @include response(1200px) {
      display: flex;
    }
  }
  &__menu-btn-open {
    .header__menu-btn.active & {
      display: none;
    }
  }
  &__menu-btn-close {
    display: none;
    .header__menu-btn.active & {
      display: block;
    }
  }
  &__logo {
    position: relative;
    flex: 0 0 auto;
    margin-right: 32px;
    text-decoration: none;
    @include response(1200px) {
      margin-right: 0;
    }
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 24px;
  }
  &__second-nav {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      .header__second-nav-arrow {
        transform: scaleY(-1);
      }
      > ul {
        opacity: 1;
        visibility: visible;
      }
    }
    > ul {
      list-style: none;
      margin: 0;
      padding: 8px;
      position: absolute;
      top: 100%;
      left: 0;
      border-radius: 8px;
      background: var(--background-bg-white-pages, #fff);
      box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
      width: 260px;
      visibility: hidden;
      opacity: 0;
      z-index: 2;
      transition: 0.3s ease-in-out;
      > li {
        @include desktop-hover() {
          > a {
            background: #f5f5f5;
            color: #000;
            color: var(--Primary-Accent, #ff3d00);
          }
        }
        > a {
          display: block;
          position: relative;
          padding: 12px 24px 12px 12px;
          color: var(--Primary-Black, #000);
          font-size: 16px;
          line-height: 24px;
          text-decoration: none;
          border-radius: 8px;
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
  &__second-nav-title {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
  }
  &__second-nav-icon {
    position: relative;
    flex: 0 0 auto;
    fill: #fff;
    margin-right: 6px;
  }
  &__second-nav-arrow {
    position: relative;
    flex: 0 0 auto;
    fill: #fff;
    margin-left: 6px;
    fill: none;
    stroke: #fff;
    transition: 0.3s ease-in-out;
  }
  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
    flex: 0 0 auto;
    @include response(1200px) {
      gap: 24px;
    }
  }
  &__controls-item {
    position: relative;
    flex: 0 0 32px;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    @include desktop-hover() {
      .header__controls-item-icon {
        display: none;
      }
      .header__controls-item-icon-active {
        display: block;
      }
    }
    @include response(1200px) {
      flex: 0 0 24px;
      width: 24px;
      height: 24px;
    }
    &--profile {
      @include response(1200px) {
        display: none;
      }
    }
    &--active {
      .header__controls-item-icon {
        display: none;
      }
      .header__controls-item-icon-active {
        display: block;
      }
    }
  }
  &__controls-item-icon {
    position: relative;
    width: 100%;
    height: 100%;
  }
  &__controls-item-icon-active {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
  }
  &__controls-item-counter {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 99px;
    background: var(--Primary-Accent, #ff3d00);
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 12px;
    line-height: 14px;
    font-weight: 500;
    height: 14px;
    min-width: 14px;
    padding-left: 4px;
    padding-right: 4px;
  }
  &__bottom {
    position: relative;
    border-bottom: 1px solid #eaeaea;
    background: var(--Primary-White, #fff);
    @include response(1200px) {
      display: none;
    }
  }
  &__nav {
    position: relative;
    > ul {
      position: relative;
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      gap: 32px;
      z-index: 1;
      @include response(1200px) {
        display: block;
      }
      > li {
        position: relative;
        @include response(1200px) {
          border-bottom: 1px solid #eaeaea;
        }
        &.menu-item-has-children {
          @include response(1200px) {
            &.active {
              > a {
                color: #646464;
                text-align: center;
                border-bottom: 1px solid #eaeaea;
                > svg {
                  display: none;
                }
              }
              > ul {
                display: block;
              }
              &.level {
                > ul {
                  > li {
                    border-bottom: 0;
                  }
                }
                > a {
                  display: none;
                }
              }
            }
          }
          > a {
            padding-right: 22px;
            @include response(1200px) {
              padding-right: 30px;
            }
            > svg {
              position: absolute;
              top: 50%;
              margin-top: -8px;
              right: 0;
              stroke: #000;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                right: 12px;
                transform: rotate(-90deg);
              }
            }
          }
        }
        @include desktop-hover() {
          > ul {
            visibility: visible;
            opacity: 1;
          }
          > a {
            color: var(--Primary-Accent, #ff3d00);
            > svg {
              stroke: var(--Primary-Accent, #ff3d00);
            }
          }
        }
        > a {
          position: relative;
          display: block;
          text-decoration: none;
          color: var(--Primary-Black, #000);
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
          padding: 11px 0;
          @include response-min(1201px) {
            transition: 0.3s ease-in-out;
          }
          @include response(1200px) {
            font-size: 18px;
            line-height: 26px;
            padding-top: 12px;
            padding-bottom: 12px;
          }
        }
        > ul {
          list-style: none;
          margin: 0;
          padding: 8px;
          position: absolute;
          top: calc(100% + 1px);
          left: 0;
          border-radius: 8px;
          background: var(--background-bg-white-pages, #fff);
          box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
          width: 220px;
          visibility: hidden;
          opacity: 0;
          transition: 0.3s ease-in-out;
          @include response(1200px) {
            display: none;
            opacity: 1;
            visibility: visible;
            width: 100%;
            box-shadow: none;
            position: relative;
            top: auto;
            left: auto;
            padding: 0;
          }
          &::before {
            content: "";
            position: absolute;
            height: 1px;
            left: 0;
            right: 0;
            top: -1px;
          }
          > li {
            @include response(1200px) {
              border-bottom: 1px solid #eaeaea;
              &:last-child {
                border-bottom: 0;
              }
            }
            &.menu-item-has-children {
              @include response(1200px) {
                &.active {
                  > a {
                    color: #646464;
                    text-align: center;
                    border-bottom: 1px solid #eaeaea;
                    > svg {
                      display: none;
                    }
                  }
                  > ul {
                    display: block;
                  }
                }
              }
              > a {
                > svg {
                  position: absolute;
                  top: 50%;
                  margin-top: -8px;
                  right: 12px;
                  stroke: #000;
                  transition: 0.3s ease-in-out;
                }
              }
            }
            @include desktop-hover() {
              > ul {
                visibility: visible;
                opacity: 1;
              }
              > a {
                background: #f5f5f5;
                color: #000;
                color: var(--Primary-Accent, #ff3d00);
                > svg {
                  stroke: var(--Primary-Accent, #ff3d00);
                }
              }
            }
            > a {
              display: block;
              position: relative;
              padding: 12px 24px 12px 12px;
              color: var(--Primary-Black, #000);
              font-size: 16px;
              line-height: 24px;
              text-decoration: none;
              border-radius: 8px;
              @include response-min(1201px) {
                transition: 0.3s ease-in-out;
              }
              @include response(1200px) {
                padding: 12px 0;
                font-size: 18px;
                font-weight: 500;
                line-height: 26px;
              }
            }
            > ul {
              list-style: none;
              margin: 0;
              padding: 8px;
              position: absolute;
              top: 0;
              left: 100%;
              border-radius: 8px;
              background: var(--background-bg-white-pages, #fff);
              box-shadow: 0px 12px 40px 0px rgba(0, 0, 0, 0.1);
              width: 220px;
              visibility: hidden;
              opacity: 0;
              transition: 0.3s ease-in-out;
              @include response(1200px) {
                display: none;
                opacity: 1;
                visibility: visible;
                width: 100%;
                box-shadow: none;
                position: relative;
                top: auto;
                left: auto;
                padding: 0;
              }
              > li {
                @include response(1200px) {
                  border-bottom: 1px solid #eaeaea;
                  &:last-child {
                    border-bottom: 0;
                  }
                }
                @include desktop-hover() {
                  > a {
                    background: #f5f5f5;
                    color: #000;
                    color: var(--Primary-Accent, #ff3d00);
                  }
                }
                > a {
                  display: block;
                  position: relative;
                  padding: 12px 24px 12px 12px;
                  color: var(--Primary-Black, #000);
                  font-size: 16px;
                  line-height: 24px;
                  text-decoration: none;
                  border-radius: 8px;
                  @include response(1200px) {
                    padding: 12px 0;
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 26px;
                  }
                  @include response-min(1201px) {
                    transition: 0.3s ease-in-out;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  &__mobile {
    display: none;
    @include response(1200px) {
      overscroll-behavior: none;
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      height: calc(100dvh - 57px);
      z-index: 3;
      background: #fff;
      padding: 24px 16px;
      opacity: 0;
      visibility: hidden;
      overflow-y: auto;
      &.active {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.back-level-btn {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    transform: none;
  }
}
