.sign-box {
  position: relative;
  padding: 16px;
  border-radius: 8px;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  &__icon {
    display: flex;
    margin-bottom: 16px;
  }
  &__text {
    position: relative;
    margin-bottom: 16px;
    color: #454545;
    text-align: center;
    font-size: 16px;
    line-height: 20px;
  }
  &__btn {
    flex: 0 0 auto;
  }
}
